"use client";

import { useEffect, useState } from "react";
import { useParams } from 'next/navigation';
import { createClient } from "@/lib/supabase/client";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import Link from "next/link";

interface Shop {
  id: string;
  name: string;
  description: string;
  logo_url: string | null;
  banner_url: string | null;
  primary_color: string;
  secondary_color: string;
  accent_color: string;
}

interface Product {
  id: string;
  name: string;
  description: string;
  price: number;
  compare_at_price: number | null;
  image_url: string | null;
}

interface Category {
  id: string;
  name: string;
}

export default function ShopPage() {
  const params = useParams();
  const shopSlug = params.slug as string;
  const supabase = createClient();

  const [shop, setShop] = useState<Shop | null>(null);
  const [products, setProducts] = useState<Product[]>([]);
  const [categories, setCategories] = useState<Category[]>([]);
  const [selectedCategory, setSelectedCategory] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [cart, setCart] = useState<{ productId: string; quantity: number }[]>([]);
  const [showCart, setShowCart] = useState(false);

  useEffect(() => {
    loadShopData();
  }, [shopSlug]);

  const loadShopData = async () => {
    try {
      // Load shop
      const { data: shopData } = await supabase
        .from("shops")
        .select("*")
        .eq("slug", shopSlug)
        .single();

      if (!shopData) return;

      setShop(shopData);

      // Load categories
      const { data: categoriesData } = await supabase
        .from("categories")
        .select("id, name")
        .eq("shop_id", shopData.id)
        .order("display_order");

      setCategories(categoriesData || []);

      // Load products
      const { data: productsData } = await supabase
        .from("products")
        .select("id, name, description, price, compare_at_price, image_url")
        .eq("shop_id", shopData.id)
        .eq("status", "published")
        .order("created_at", { ascending: false });

      setProducts(productsData || []);
    } catch (error) {
      console.error("Error loading shop:", error);
    } finally {
      setIsLoading(false);
    }
  };

  const filteredProducts = selectedCategory
    ? products.filter(p => p.category_id === selectedCategory)
    : products;

  const cartTotal = cart.reduce((sum, item) => {
    const product = products.find(p => p.id === item.productId);
    return sum + ((product?.price || 0) * item.quantity);
  }, 0);

  const addToCart = (productId: string) => {
    setCart(prev => {
      const existing = prev.find(item => item.productId === productId);
      if (existing) {
        return prev.map(item =>
          item.productId === productId
            ? { ...item, quantity: item.quantity + 1 }
            : item
        );
      }
      return [...prev, { productId, quantity: 1 }];
    });
  };

  const removeFromCart = (productId: string) => {
    setCart(prev => prev.filter(item => item.productId !== productId));
  };

  const updateQuantity = (productId: string, quantity: number) => {
    if (quantity <= 0) {
      removeFromCart(productId);
    } else {
      setCart(prev =>
        prev.map(item =>
          item.productId === productId ? { ...item, quantity } : item
        )
      );
    }
  };

  const handleCheckout = async () => {
    if (cart.length === 0) return;

    try {
      const { data: { user } } = await supabase.auth.getUser();

      if (!shop) return;

      // Create order
      const { data: order, error: orderError } = await supabase
        .from("orders")
        .insert({
          shop_id: shop.id,
          customer_name: "Guest Customer",
          customer_email: "guest@example.com",
          total_amount: cartTotal,
          status: "pending",
          payment_method: "cash_on_delivery",
        })
        .select()
        .single();

      if (orderError) throw orderError;

      // Create order items
      const orderItems = cart.map(item => {
        const product = products.find(p => p.id === item.productId);
        return {
          order_id: order.id,
          product_id: item.productId,
          product_name: product?.name || "",
          quantity: item.quantity,
          price: product?.price || 0,
        };
      });

      const { error: itemsError } = await supabase
        .from("order_items")
        .insert(orderItems);

      if (itemsError) throw itemsError;

      alert("Order placed successfully! We'll contact you soon.");
      setCart([]);
      setShowCart(false);
    } catch (error) {
      console.error("Error placing order:", error);
      alert("Failed to place order. Please try again.");
    }
  };

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-background">
        <div className="text-muted-foreground">Loading shop...</div>
      </div>
    );
  }

  if (!shop) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-background">
        <div className="text-muted-foreground">Shop not found</div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <header className="border-b border-border sticky top-0 z-40 bg-card">
        <div className="max-w-7xl mx-auto px-6 py-4 flex justify-between items-center">
          <div>
            {shop.logo_url && (
              <img src={shop.logo_url || "/placeholder.svg"} alt={shop.name} className="h-8 w-auto" />
            )}
            {!shop.logo_url && (
              <h1 className="text-xl font-bold">{shop.name}</h1>
            )}
          </div>

          <Button
            onClick={() => setShowCart(!showCart)}
            variant="outline"
            className="relative"
          >
            ðŸ›’ Cart
            {cart.length > 0 && (
              <span className="absolute -top-2 -right-2 bg-red-500 text-white text-xs rounded-full w-5 h-5 flex items-center justify-center">
                {cart.length}
              </span>
            )}
          </Button>
        </div>
      </header>

      <div className="flex">
        {/* Sidebar */}
        <aside className="w-48 border-r border-border p-6 bg-card min-h-[calc(100vh-73px)]">
          <h3 className="font-semibold mb-4">Categories</h3>
          <div className="space-y-2">
            <button
              onClick={() => setSelectedCategory(null)}
              className={`w-full text-left px-3 py-2 rounded ${
                selectedCategory === null
                  ? "bg-primary text-primary-foreground"
                  : "hover:bg-muted"
              }`}
            >
              All Products
            </button>
            {categories.map(cat => (
              <button
                key={cat.id}
                onClick={() => setSelectedCategory(cat.id)}
                className={`w-full text-left px-3 py-2 rounded ${
                  selectedCategory === cat.id
                    ? "bg-primary text-primary-foreground"
                    : "hover:bg-muted"
                }`}
              >
                {cat.name}
              </button>
            ))}
          </div>
        </aside>

        {/* Main Content */}
        <main className="flex-1">
          {showCart ? (
            <div className="p-8">
              <h2 className="text-3xl font-bold mb-6">Shopping Cart</h2>

              {cart.length === 0 ? (
                <div className="text-muted-foreground text-center py-12">
                  Your cart is empty
                </div>
              ) : (
                <div className="space-y-6">
                  <div className="space-y-4">
                    {cart.map(item => {
                      const product = products.find(p => p.id === item.productId);
                      if (!product) return null;

                      return (
                        <Card key={item.productId}>
                          <CardContent className="p-4">
                            <div className="flex justify-between items-center">
                              <div>
                                <h4 className="font-semibold">{product.name}</h4>
                                <p className="text-sm text-muted-foreground">
                                  ${product.price} x {item.quantity} = ${(product.price * item.quantity).toFixed(2)}
                                </p>
                              </div>
                              <div className="flex gap-2 items-center">
                                <input
                                  type="number"
                                  min="1"
                                  value={item.quantity}
                                  onChange={(e) => updateQuantity(item.productId, parseInt(e.target.value))}
                                  className="w-16 px-2 py-1 border border-border rounded"
                                />
                                <Button
                                  variant="destructive"
                                  size="sm"
                                  onClick={() => removeFromCart(item.productId)}
                                >
                                  Remove
                                </Button>
                              </div>
                            </div>
                          </CardContent>
                        </Card>
                      );
                    })}
                  </div>

                  <Card>
                    <CardContent className="p-6">
                      <div className="space-y-3">
                        <div className="flex justify-between font-semibold text-lg">
                          <span>Total:</span>
                          <span>${cartTotal.toFixed(2)}</span>
                        </div>
                        <Button
                          onClick={handleCheckout}
                          className="w-full"
                          style={{ backgroundColor: shop.accent_color }}
                        >
                          Checkout
                        </Button>
                        <Button
                          variant="outline"
                          onClick={() => setShowCart(false)}
                          className="w-full"
                        >
                          Continue Shopping
                        </Button>
                      </div>
                    </CardContent>
                  </Card>
                </div>
              )}
            </div>
          ) : (
            <div className="p-8">
              <div className="mb-8">
                <h2 className="text-3xl font-bold">{shop.name}</h2>
                {shop.description && (
                  <p className="text-muted-foreground mt-2">{shop.description}</p>
                )}
              </div>

              {products.length === 0 ? (
                <div className="text-muted-foreground text-center py-12">
                  No products available
                </div>
              ) : (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                  {filteredProducts.map(product => (
                    <Card key={product.id} className="overflow-hidden hover:shadow-lg transition-shadow">
                      {product.image_url && (
                        <img src={product.image_url || "/placeholder.svg"} alt={product.name} className="w-full h-48 object-cover" />
                      )}
                      {!product.image_url && (
                        <div className="w-full h-48 bg-muted flex items-center justify-center">
                          <span className="text-muted-foreground">No image</span>
                        </div>
                      )}
                      <CardContent className="p-4">
                        <h3 className="font-semibold text-lg mb-2">{product.name}</h3>
                        {product.description && (
                          <p className="text-sm text-muted-foreground mb-4 line-clamp-2">
                            {product.description}
                          </p>
                        )}

                        <div className="mb-4">
                          <div className="flex gap-2 items-baseline">
                            <span className="text-2xl font-bold" style={{ color: shop.primary_color }}>
                              ${product.price}
                            </span>
                            {product.compare_at_price && (
                              <span className="text-sm line-through text-muted-foreground">
                                ${product.compare_at_price}
                              </span>
                            )}
                          </div>
                        </div>

                        <Button
                          onClick={() => addToCart(product.id)}
                          className="w-full"
                          style={{ backgroundColor: shop.primary_color }}
                        >
                          Add to Cart
                        </Button>
                      </CardContent>
                    </Card>
                  ))}
                </div>
              )}
            </div>
          )}
        </main>
      </div>
    </div>
  );
}
